import Tab from '../../common_ui/tab';
import uiTabContentsMyAccount from './ui_components/settings.ui.my_account';
import uiTabContentsBroadcast from './ui_components/settings.ui.broadcast';
import uiTabContentsSubscribe from './ui_components/settings.ui.subscribe';

let uiSettingsPanel = null;
$(document).ready( (e) => {
    uiSettingsPanel = new Tab();
    uiSettingsPanel.init('#settings-panel', [null, null, null, uiTabContentsSubscribe.initData]);
    uiTabContentsMyAccount.init();
    uiTabContentsBroadcast.init();
    uiTabContentsSubscribe.init();
})