import { BroadcastService } from '../../../services/settings.service';
import LiveService from '../../../services/live.service';
import { CHANNEL_INFO } from '../../live/store/live.store';
export default (() => {
    let $tab_contents_broadcast = null;
    let $btn_update_image = null;
    let $input_file = null;
    let $img_broadcast = null;
    let $toggle_ignore_join_live = null;
    let $toggle_ignore_join_video = null;
    let $btn_clear_join_live = null;
    let $btn_clear_join_video = null;
    const init = async () => {
        $tab_contents_broadcast = $('#tab-contents-broadcast');
        $btn_update_image = $tab_contents_broadcast.find('#btn-update-broadcast-img');
        $input_file = $tab_contents_broadcast.find('#input-file-broadcast-img');
        $img_broadcast = $tab_contents_broadcast.find('.broadcast-img')
        $toggle_ignore_join_live = $tab_contents_broadcast.find('#switch-join-live');
        $toggle_ignore_join_video = $tab_contents_broadcast.find('#switch-join-video');
        $btn_clear_join_live = $tab_contents_broadcast.find('#btn-clear-join-live');
        $btn_clear_join_video = $tab_contents_broadcast.find('#btn-clear-join-video');

        bindEventHandler();

        const resGetUserInfo = await LiveService.getChannelInfo(USER_SESSION.user_seq);
        CHANNEL_INFO.set(resGetUserInfo.data.channel_info);

        if(CHANNEL_INFO.get().thumbnail_path) {
            $img_broadcast.css('background-image', `url('${CHANNEL_INFO.get().thumbnail_path}')`);
        } else {
            $img_broadcast.css('background-image', `url('/static/cojam/res/img-channel_bg_default.png')`);
        }

        $toggle_ignore_join_live.prop('checked', CHANNEL_INFO.get().live_join_yn === "Y" ? true : false);
        $toggle_ignore_join_video.prop('checked', CHANNEL_INFO.get().vod_join_yn === "Y" ? true : false);
    }

    const bindEventHandler = () => {
        $btn_update_image.click((e) => {
            e.preventDefault();
            $input_file.trigger('click')
        })

        $input_file.change(async (e) => {
            e.preventDefault();
            const fileInfo = $(e.target).get(0).files[0];
            if (fileInfo.type !== "image/png" && fileInfo.type !== "image/jpeg") {
                alert($.i18n.t('sel_file_png_jpeg'))
                return false;
            }

            const resGetUploadUrl = await BroadcastService.getUploadUrl(
                USER_SESSION.user_seq,
                fileInfo.name,
                fileInfo.size
            );

            const { url } = resGetUploadUrl.data.info;

            await BroadcastService.uploadImage(url, fileInfo, (percent) => { console.log(percent) })

            let reader = new FileReader();
            reader.onload = function (e) {
                $img_broadcast.css('background-image', `url('${e.target.result}')`);
            };

            reader.readAsDataURL(fileInfo);
        })

        $toggle_ignore_join_live.change((e) => {
            e.preventDefault();
            const val = $toggle_ignore_join_live.prop('checked') ? "Y" : "N";
            LiveService.setIgnoreJoin_Live(CHANNEL_INFO.get().channel_seq, val);
        })

        $toggle_ignore_join_video.change((e) => {
            e.preventDefault();
            const val = $toggle_ignore_join_video.prop('checked') ? "Y" : "N";
            LiveService.setIgnoreJoin_Video(CHANNEL_INFO.get().channel_seq, val);
        })

        // $btn_clear_join_live.click((e) => {
        //     e.preventDefault();
        //     LiveService.clearJoinList_Live(LIVE_INFO.get().live_seq, CHANNEL_INFO.get().channel_seq);
        //     clear();
        // })

        $btn_clear_join_video.click((e) => {
            e.preventDefault();
            LiveService.clearJoinList_Video(CHANNEL_INFO.get().channel_seq)
                .then(res => {
                    const { status, message } = res;
                    if (status === 200) {
                        clear();
                    } else {
                        alert($.i18n.t('wrng_reset_fail'))
                    }
                })
        })
    }

    return {
        init
    }
})()