import { AccountService } from '../../../services/settings.service';

export default (() => {
    const MAX_NAME_LENGTH = 10; 
    let $tab_contents_account = null;
    let $btn_update_name = null;
    let $btn_update_profile_img = null;
    let $btn_leave = null;
    let $input_name = null;
    let $input_file = null;
    let $img_profile = null;
    const init = () => {
        $tab_contents_account = $('#tab-contents-account');
        $input_name = $('#input-account-name');
        $tab_contents_account.find('#lbl-account-email').text(USER_SESSION.user_id);
        $input_name.val(USER_SESSION.nick_name);
        
        $btn_update_name = $('#btn-account-update-name');
        $btn_update_profile_img = $tab_contents_account.find('#btn-account-update-profile-img');
        $btn_leave = $tab_contents_account.find('#btn-leave');
        
        $input_file = $tab_contents_account.find('#input-file-profile-img');

        $img_profile = $tab_contents_account.find('#img-profile');
        $img_profile.attr('src', USER_SESSION.avatar_url)
        // $input_file.hide();
        bindEventHandler();
    }

    const bindEventHandler = () => {
        $input_name.on('propertychange change keyup paste input', () => {
            const inputText = $input_name.val();

            // 입력이 제한 길이보다 길면 자른다.
            if (inputText.length > MAX_NAME_LENGTH) {
                $input_name.val(inputText.substring(0, MAX_NAME_LENGTH));
                // setTitleLength(MAX_NAME_LENGTH);
            }

            // setTitleLength(inputText.length);
        });

        $btn_update_name.click( (e) => {
            e.preventDefault();
            const changeName = $input_name.val();
            AccountService.updateName(USER_SESSION.user_seq, changeName)
            .then( res => {console.log()})
            .catch( err => {console.log(err)})
        })

        $btn_update_profile_img.click( (e) => {
            e.preventDefault();
            $input_file.trigger('click')
        })

        $input_file.change( async (e) => {
            e.preventDefault();
            const fileInfo = $(e.target).get(0).files[0];
            // console.log(fileInfo)
            if(fileInfo.type !== "image/png" && fileInfo.type !== "image/jpeg") {
                alert($.i18n.t('sel_file_png_jpeg'))
                return false;
            }

            const resGetUploadUrl = await AccountService.getUploadProfileUrl(
                USER_SESSION.user_seq,
                fileInfo.name,
                fileInfo.size
            );

            const { url } = resGetUploadUrl.data.info;

            await AccountService.uploadProfileImg(url, fileInfo, (percent) => { console.log(percent)})
            
            let reader = new FileReader();
            reader.onload = function (e) {
                console.log("on load")
                $img_profile.attr('src', e.target.result)
            };

            reader.readAsDataURL(fileInfo);
        })
        
        $btn_leave.click( (e) => {
            e.preventDefault();
            location.href = "/leave";
        })
    }

    return {
        init
    }
})()
