import Fetch from '../utils/Fetch';

export const AccountService = {
    updateName: (userSeq, name) => {
        return Fetch({
            url: `/api/v1/user/nicknm`,
            type: 'PUT',
            data: JSON.stringify({
                user_seq: userSeq,
                nick_name: name
            })
        })
    },

    leave: (verifyEmail, desc) => {
        return Fetch({
            url: `/api/v1/user/withdrawal`,
            type: 'DELETE',
            data: JSON.stringify({
                user_id: verifyEmail,
                description: desc
            })
        })
    },

    getUploadProfileUrl: (userSeq, fileName, fileSize) => {
        return Fetch({
            url: `/api/v1/user/profileimg`,
            type: 'PUT',
            data: JSON.stringify({
                user_profile_img: {
                    user_seq: userSeq
                },
                file_upload_vo: {
                    upload_service_type: "profile",
                    file_type: "image",
                    file_name: fileName,
                    content_length: fileSize
                }
            })
        })
    },
    uploadProfileImg: (url, data, progressEvent) => {
        return uploadImage(url, data, progressEvent);
    }
}


export const BroadcastService = {
    getUploadUrl: (channelSeq, fileName, fileSize) => {
        return Fetch({
            url: `/api/v1/channel/thumbnail`,
            type: 'PUT',
            data: JSON.stringify({
                channel_thumbnail: {
                    channel_seq: channelSeq
                },
                file_upload_vo: {
                    upload_service_type: "channel_thumbnail",
                    file_type: "image",
                    file_name: fileName,
                    content_length: fileSize
                }
            })
        })
    },
    uploadImage: (url, data, progressEvent) => {
        return uploadImage(url, data, progressEvent);
    }
}

const uploadImage = (url, data, progressEvent) => {
    return new Promise((resolve, reject) => {
        let xhr = null;
        xhr = $.ajax({
            url,
            type: 'PUT',
            headers: {
                'x-amz-acl': 'authenticated-read'
                // ...header,
                // ...meta
            },
            contentType: 'binary/octet-stream',
            processData: false,
            data,
            xhr: function () {
                var xhr = new window.XMLHttpRequest();
                //Upload progress
                if (progressEvent) {
                    xhr.upload.addEventListener("progress", function (evt) {
                        if (evt.lengthComputable) {
                            var percentComplete = (evt.loaded / evt.total);
                            progressEvent(percentComplete);
                        }
                    }, false);
                }
                return xhr;
            },
            success: function (res) {
                xhr = null;
                return resolve(res);
            },
            error: function (err) {
                xhr = null;
                console.log("upload error")
                return reject(err);
            }
        })

    })
}
