import CommonService from "../../../services/common.service";
import SubscribeButton from '../../../common_ui/SubscribeButton';

import LiveService from '../../../services/live.service';

let $contents_container = null;
let $list = null;
let listData = [];
let uiSubscribeButtons = [];
export default (()=>{
    const init = () => {
        $contents_container = $('#tab-contents-subscribe');
        $list = $('#subscribe-list');
    }
    
    const initData = async () => {
        const resSubscribeList = await CommonService.getMySubscribeList(0, 20);
 
        listData = [];
        listData.push(...resSubscribeList.data.list)
        $list.empty();
        resSubscribeList.data.list.map( info => {
            $list.append(
                createCard(
                    info.profile_img,
                    info.nick_name,
                    "N",
                    info.vod_cnt,
                    info.subscription_cnt,
                    info.user_seq,
                    info.channel_seq,
                    info.live_seq
                )
            )
            let subScribeButton = new SubscribeButton();
            subScribeButton.init($('.channel-info:last'))
            subScribeButton.setChannelSeq(info.channel_seq)
            uiSubscribeButtons.push(subScribeButton);
        })

        bindEventList();
    }

    const bindEventList = () => {
        $(`.channel-info .avratar-container,
            .channel-info .name-container,
            .channel-info .video-count`).click( e => {
            const userSeq = $(e.currentTarget).closest('.channel-info').attr('data-user-seq');
            location.href = `/channel/${userSeq}`
        })

        $(`.channel-info .onair-status`).click(async (e) => {
            const index = $('.channel-info').index($(e.currentTarget).closest('.channel-info'));
            const userSeq = listData[index].user_seq;
            const resGetLive = await LiveService.getRecommendLive(userSeq, 0, 1);
            if (!resGetLive || !resGetLive.data || !resGetLive.data.live || resGetLive.data.live.length === 0) {
                alert($.i18n.t('wrng_not_load_cast_info'));
                return;
            }
            const live_seq = resGetLive.data.live[0].live_seq;
            location.href = `/live/${live_seq}`;
        })
    }

    return {
        init, initData
    }
})()


const createCard = (profile_img, name, onair, vod_cnt, subscription_cnt, user_seq, channel_seq, live_seq) => {
    return `
    <li class="channel-info" data-user-seq=${user_seq} data-channel-seq=${channel_seq}>
        <div class="left">
            <span class="avatar-container">
                <img src=${profile_img}>
            </span>
            <span class="name-container bold">${name}</span>
            <span class="onair-status fs-xxs bold fc-white${live_seq !== 0 ? ' on' : ''}">ON AIR</span>
            <span class="video-count">
                <span class="fs-xs">${i18next.t('vods')}&nbsp;<span class="count fc-light-green">${vod_cnt}</span></span>
            </span>
        </div>
        <div class="right">
            <span class="subscribe-count fs-xs">${i18next.t('subscriber')}&nbsp;<span class="count fc-light-green">${subscription_cnt}</span></span>
            <button class="btn-subscribe btn rnd mid tr on"><i class="check-icon"></i><span>${i18next.t('btn_subscribed')}</span></button>
        </div>
    </li>`
}